function getSearchOptions(options) {
  return options.reduce((acc, option) => {
    if (!option.title || option?.title.trim().length <= 0) {
      return acc;
    }
    return [
      ...acc,
      {
        label: option.title,
        value: option.title,
      },
    ];
  }, []);
}

export default getSearchOptions;
