const groupBy = (arr, key) => {
  return arr.reduce((prev, curr) => {
    if (!curr[key]) {
      return prev;
    }
    if (prev[curr[key]]) {
      prev[curr[key]] = curr;
    } else {
      prev[curr[key]] = curr;
    }
    return prev;
  }, {});
};

export default groupBy;
